// Bootstrap
import Button from "react-bootstrap/Button";
import ListGroup from "react-bootstrap/ListGroup";
import { useTranslation } from "react-i18next";
import Icon from "../../Shared/Icon/Icon";

const SideBar = () => {
  const { t } = useTranslation('profile');

  const sidebarItems = [
    {
      id: 1,
      name: t('profile'),
      icon: "user",
      href: "#edit"
    },
    {
      id: 2,
      name: t('sidebar.membership'),
      icon: "membership",
      href: "#membership"
    },
    {
      id: 3,
      name: t('sidebar.history'),
      icon: "history",
      href: "#history"
    },
    {
      id: 4,
      name: t('sidebar.transactions'),
      icon: "transaction",
      href: "#transactions"
    },
    {
      id: 5,
      name: t('sidebar.fav'),
      icon: "heart",
      href: "#fav"
    }

  ]
  return (
    <div className="profile__sidebar d-md-flex flex-column justify-content-between">
      <div>
        <div className="bg-gray-300 text-center mb-3 p-4 profile__sidebar__user">
          <Icon iconName="user" iconSize={130} variant="primary" className="bg-white rounded-pill p-3 opacity-50" />
          <h4>user name</h4>
        </div>
        <ListGroup variant="flush">
          {
            sidebarItems.map((item) => (
              <ListGroup.Item action href={item.href} key={item.id} className="py-3">
                <Icon iconName={item.icon} className="me-3" variant="primary" iconSize={18} />
                <small className="fs-8 text-blue">{item.name}</small>
              </ListGroup.Item>
            ))
          }
        </ListGroup>
      </div>
      <div className="px-3 mb-3">
        <Icon iconName="signout" className="me-3" variant="primary" iconSize={18} />
        <Button variant="link" className="fs-8 text-danger p-0"> {t("sidebar.signOut")} </Button>
      </div>
    </div>
  )
};

export default SideBar;