// Bootstrap
import Tab from "react-bootstrap/Tab";
import ProfileHistory from "./ProfileHistory";
import ProfileTransactions from "./ProfileTransactions";
import ProfileFavorites from "./ProfileFavorites/ProfileFavorites";

interface IfavoritesProps {
  id: string,
  image: string,
  description: string
}

interface IMainContentProps {
  favorites: IfavoritesProps[]
}

const MainContent = ({favorites}: IMainContentProps) => {
  const tabContentItems = [
    {
      id: 1,
      content: <h1>edit</h1>,
      href: "#edit"
    },
    {
      id: 2,
      content: <h1>membership</h1>,
      href: "#membership"
    },
    {
      id: 3,
      content: <ProfileHistory />,
      href: "#history"
    },
    {
      id: 4,
      content: <ProfileTransactions />,
      href: "#transactions"
    },
    {
      id: 5,
      content: <ProfileFavorites favorites={favorites} />,
      href: "#fav"
    }
  ]
  return (
    <div className="bg-white rounded-end shadow-sm p-4 flex-grow-1">
      <Tab.Content>
        {
          tabContentItems.map((item) => (
            <Tab.Pane eventKey={item.href} key={item.id}>
              {item.content}
            </Tab.Pane>
          ))
        }
      </Tab.Content>
    </div>
  )
}
export default MainContent;