import { useTranslation } from "react-i18next";

import ListGroup from "react-bootstrap/ListGroup";

import { DefaultMomentFormat } from "../../../core/Mixins/MomentFormats";

const ProfileTransactions = () => {
  const { t } = useTranslation(["translation", "profile"]);

  const transactionsItems = [
    {
      id: 1,
      name: "كورس الاقتصاد و المالية",
      price: "3221",
      date: "2022-1-1 13:23:44"
    },
    {
      id: 2,
      name: "ندوة الثقافة",
      price: "345",
      date: "2022-1-1 13:23:44"
    },
    {
      id: 3,
      name: "كورس الاقتصاد و المالية",
      price: "700",
      date: "2022-1-1 13:23:44"
    },
  ];

  return (
    <>
      <h2 className="profile__title text-blue mb-4"> {t("profile:transactions.transactions")} </h2>
      <div className="d-none d-md-flex p-3">
        <small className="text-light-purple w-md-50">{t("profile:transactions.name")}</small>
        <small className="text-light-purple flex-fill">{t("profile:transactions.price")}</small>
        <small className="text-light-purple flex-fill">
        {t("profile:transactions.date")}
        </small>
      </div>
      <ListGroup className="profile__list">
        {
          transactionsItems.map((transaction) => (
            <ListGroup.Item className="mb-3 profile__list d-md-flex" key={transaction.id}>
              <h5 className="text-blue mb-2 mb-md-0 w-md-50">{transaction.name}</h5>
              <p className="fs-7 mb-2 mb-md-0 text-light-purple flex-fill">{transaction.price} {t("translation:currency")}</p>
              <p className="fs-7 text-light-purple flex-fill">
                {t("profile:transactions.day")} {DefaultMomentFormat(transaction.date)}
              </p>
            </ListGroup.Item>
          ))
        }
      </ListGroup>
    </>
  )
};

export default ProfileTransactions