import { useEffect, useState } from "react";
import { Helmet } from "react-helmet";
import { useTranslation } from "react-i18next";

import Tab from "react-bootstrap/Tab";
import Container from "react-bootstrap/Container";


// Components
import Header from "../../components/Layout/Header/Header";
import Footer from "../../components/Layout/Footer/Footer";
import MainContent from "../../components/Profile/mainContent/MainContent";
import SideBar from "../../components/Profile/SideBar/SideBar";

import FavoritesData from "../../core/Data/Profile/Favorites";

import "./ProfileStyle.scss"

const ProfileHistory = () => {
  const { t } = useTranslation("profile");

  const [favorites, setFavorites] = useState<any>([])

  const getfavorites = () => {
      try {
        const res = FavoritesData;
        setFavorites(res.favorites)
      } catch (e) {
        console.log(e)
      }
  }

  useEffect(()=> {
    getfavorites();
  })

  return (
    <>
      <Helmet>
        <title>{t('profile')}</title>
      </Helmet>

      <Header title={t("profile")}>
        <Container fluid>
          <Tab.Container id="list-group-tabs-example" defaultActiveKey="#edit">
            <div className="bg-white rounded-3 profile__content d-lg-flex">
              <SideBar />
              <MainContent favorites={favorites} />
            </div>
          </Tab.Container>
        </Container>
      </Header>

      <Footer />
    </>
  )
}

export default ProfileHistory;