
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Image from "react-bootstrap/Image";

interface IFavoritesItem {
  image: string
  description: string,
};

const FavoriteItem = ({
  image,
  description,
}: IFavoritesItem) => {
  return (
    <Col>
      <Card className="flex-md-row rounded-3 p-2 align-items-center align-items-md-start">
        <div className="profile__favorite__image">
        <Image src={image} width={205} height={148}  className="rounded" />
        </div>
        <Card.Body className="py-md-0">
          <h6 className="text-dark-purple lh-lg">{description}</h6>
        </Card.Body>
      </Card>
    </Col>
  )
}

export default FavoriteItem;