import Row from "react-bootstrap/Row";
import { useTranslation } from "react-i18next";
import FavoriteItem from "./FavoriteItem";

interface IFavoriteProps {
  id: string,
  image: string,
  description: string
};

interface IFavoritesProps {
  favorites: IFavoriteProps[]
}

const ProfileFavorites = ({favorites}:IFavoritesProps) => {
  const { t } = useTranslation("profile");
  return (
    <>
      <h2 className="profile__title text-blue mb-4"> {t("favorites.favorites")} </h2>
      <Row xs={1} md={2} className="g-4 profile__favorite">
        {favorites.map((fav)=> (
          <FavoriteItem {...fav} key={fav.id} />
        ))
        }
      </Row>
    </>
  )
}

export default ProfileFavorites;